* {
  margin: 0;
  padding: 0;
  font-family: 'Barlow', sans-serif;
  font-family: 'Exo', sans-serif;
  box-sizing: border-box;
  @import url('https://fonts.googleapis.com/css2?family=Barlow:wght@200&family=Exo:wght@100&display=swap');
}

.outer-div {
  width: 100vw;
  height: 100vh;
  display: grid;
  place-items: center;
  background-color: rgba(39, 39, 49);
}

.inner-div {
  width: 30vw;
  height: 90vh;
  box-shadow: 6px 6px 10px black;
  display: flex;
  gap: 0.2rem;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  background-color: teal;
}

.head-div {
  width: 100%;
  height: 15vh;
  color: #00203FFF; 
  display: flex;
  justify-content: center;
  align-items: center;

}

.listAdd-div {
  width: 100%;
  height: 15vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.listSection-div {
  width: 100%;
  height: 100%;
  padding: 5px;
  border: 1px solid rgb(0, 255, 251);
  overflow-y: auto;
  background-color: #ADEFD1FF;
}

.eachList-div {
  width: 100%;
  height: 10vh;
  border: 1px solid white;
  margin-top: 0.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #00203FFF;

}

.listAdd-input {
  width: 75%;
  height: 8vh;
  border: none;
  padding-left: 5px;
  font-size: 4vh;
  color: rgb(0, 0, 0);
  background-color: #ADEFD1FF;
}

.listAdd-btn {
  height: 8vh;
  border: none;
  color: white;
  font-size: 3vh;
  background-color: #00203FFF;
  padding: 1vh;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}
.listAdd-btn:hover {
background-color: rgb(0, 114, 31);
}

.eachList-value {
  width: 80%;
  height: 9.5vh;
  color: white;
  font-size: 3vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow-x: auto;
  padding-left: 10px;
}

.removeList-btn {
  width: 18%;
  height: 8vh;
  font-size: 5vh;
  display: grid;
  place-items: center;
  background-color: transparent;
  cursor: pointer;
  border: none;
  transition: 0.3s ease-in-out;

}
.removeList-btn:hover{
 background-color: rgb(255, 50, 50);

}
.removeAll-btn{
  background-color: #00203FFF;
  color: white;
  width: 100px;
  font-size: 15px;
  height: 5vh;
  border: none;
  cursor: pointer;
  transition: 0.3s ease-in-out;

}
.removeAll-btn:hover{
  background-color: rgb(199, 15, 15);

}
/* 970 media queries */

@media screen and (max-width:970px){

  .inner-div{
    width: 100vw;
    height: 100%;
  }
  .removeList-btn{
    width: auto;
  }
 
  
}